import React, { useState } from "react";
import { Document, Page } from "react-pdf/dist/esm/entry.webpack";
import { Button } from 'antd'
export default function SinglePage(props) {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1); //setting 1 to show fisrt page
  const [scale, setScale] = useState(1); //setting 1 to show fisrt page
  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
    setPageNumber(1);
  }

  function onPageLoad(page) {
    const parentDiv = document.querySelector('#pdfDocument')
    let pageScale = parentDiv.clientWidth / page.originalWidth
    if (scale !== pageScale) {
      setScale( 0.5 * pageScale );
    }
  }

  function changePage(offset) {
    setPageNumber(prevPageNumber => prevPageNumber + offset);
  }

  function previousPage() {
    changePage(-1);
  }

  function nextPage() {
    changePage(1);
  }

  const { pdf } = props;

  return (
    <>
    <div id="pdfDocument">
      <Document
        file={pdf}
        options={{ workerSrc: "/pdf.worker.js" }}
        onLoadSuccess={onDocumentLoadSuccess}
        onLoadError={(error) => {console.error(); console.log("Error loading pdf"); console.log(error);console.error(); }}
      >
        <Page 
          pageNumber={pageNumber}
          onLoadSuccess={onPageLoad}
          scale={scale}  
        />
      </Document>
      <div>
        <Button type="Button" disabled={pageNumber <= 1} onClick={previousPage}>
          Previous
        </Button>
        <span style={{ margin : 20 }}>
          {pageNumber || (numPages ? 1 : "--")}/{numPages || "--"}
        </span>
        <Button
          type="Button"
          disabled={pageNumber >= numPages}
          onClick={nextPage}
        >
          Next
        </Button>
      </div>
    </div>
    </>
  );
}
